import styles from "../styles/styles.module.scss";
import { HeaderComponent } from "../components/header";
import { ModelsСlotch } from "../components/modelsClotch";
import WinterBlueMen from "../assets/summerTime/menCollection/WinterBlueMen.webp";
import WinterWhiteMen from "../assets/summerTime/menCollection/WinterWhiteMen.webp";
import WinterBrownMen from "../assets/summerTime/menCollection/WinterBrownMen.webp";
import WinterGreenMen from "../assets/summerTime/menCollection/WinterGreenMen.webp";
import WinterWhiteWomen from "../assets/summerTime/womenCollection/WinterWhite.webp";
const Index = () => {
  return (
    <body>
      <div className={styles.body}>
        <HeaderComponent />
        <h1 className={styles.title}>Meraviglioso</h1>
        <p className={styles.subtitle}>
          La vostra magnificenza, incarnata nello stile
        </p>
      </div>
      <div>
        <ModelsСlotch
          titleSection="Winter clotch for men"
          imageOne={WinterBlueMen}
          imageTwo={WinterWhiteMen}
          imageThree={WinterBrownMen}
          imageFour={WinterGreenMen}
        />

         <ModelsСlotch
          titleSection="Winter clotch for woomen"
          imageOne={WinterWhiteWomen}
          imageTwo={WinterWhiteWomen}
          imageThree={WinterWhiteWomen}
          imageFour={WinterWhiteWomen}
        />
      </div>
    </body>
  );
};

export default Index;
